.order-section {
  position: relative;
}
.order-section .section-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  max-height: 400px;
}
.order-section .section-content {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
.order-section .section-content .product-left {
  font-family: 'Montserrat', sans-serif;
  color: white;
  text-shadow: 0 0 5px black;
  font-size: 30px;
  font-weight: bold;
  margin-bottom: 25px;
}
.order-section .section-content .order-btn {
  cursor: pointer;
}
.order-section .section-content .order-btn {
  background-image: linear-gradient(to right, #107cff 0%, #79CBCA 51%, #77A1D3 100%);
}
.order-section .section-content .order-btn {
  margin: 10px;
  padding: 15px 45px;
  text-align: center;
  text-transform: uppercase;
  transition: 0.5s;
  background-size: 200% auto;
  color: white;
  border-radius: 10px;
  display: block;
}
.order-section .section-content .order-btn:hover {
  background-position: right center;
  /* change the direction of the change here */
  color: #fff;
  text-decoration: none;
}
@media (max-width: 991.98px) {
  .order-section {
    min-height: 300px;
  }
  .order-section .section-image {
    width: 100%;
    min-height: 300px;
  }
  .order-section .section-content .product-left {
    font-size: 20px !important;
  }
}
