.product-info-section .product-image-container {
  max-height: 400px;
}
.product-info-section .product-image-container .product-image {
  width: 100%;
  height: 100%;
  object-fit: contain;
  transition-duration: 0.5s;
  max-height: 400px;
}
.product-info-section .product-image-container:hover .product-image {
  transform: scale(1.05);
  transition-duration: 0.2s;
}
.product-info-section .product-details {
  font-family: 'Montserrat', sans-serif;
  margin-top: 30px;
  text-align: left;
}
.product-info-section .product-details .name {
  font-weight: bolder;
  font-size: 20px;
}
.product-info-section .product-details .sell-price {
  display: inline-block;
  font-weight: 400;
  font-size: 20px;
  color: orangered;
  margin-right: 5px;
}
.product-info-section .product-details .original-price {
  display: inline-block;
  font-size: 24px;
  font-weight: 600;
  text-decoration: line-through;
}
.product-info-section .product-details .about {
  line-height: 24px;
}
@media (max-width: 991.98px) {
  .product-info-section .product-image-container .product-image {
    max-height: 250px;
  }
  .product-info-section .product-details {
    padding-left: 10px;
  }
}
