.app-navbar {
  position: relative;
  box-shadow: 0 2px 3px solid #ddd;
  z-index: 999999;
}
.app-navbar .content-container {
  padding: 3px 0;
}
.app-navbar .content-container .content-body {
  transform: translate(0, 3px);
  text-align: center !important;
}
.app-navbar::after {
  content: "";
  position: absolute;
  display: inline-block;
  left: 0;
  bottom: -1px;
  width: 100%;
  height: 5px;
  background-color: white;
}
