.main-slider {
  position: relative;
  height: 400px;
}
.main-slider .image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  filter: blur(2px);
  position: relative;
}
.main-slider::before {
  content: "";
  position: absolute;
  display: inline-block;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 150px;
  opacity: 0.7;
  background: linear-gradient(to bottom, rgba(0, 0, 0, 0), black);
  z-index: 999;
}
.main-slider .slider-content {
  position: absolute;
  left: 50%;
  bottom: 30%;
  transform: translate(-50%, 50%);
  z-index: 99999;
}
.main-slider .slider-content .title {
  font-family: 'Montserrat', sans-serif;
  color: white;
  font-size: 30px;
}
.main-slider .slider-content .product-name {
  font-family: 'Montserrat', sans-serif;
  color: #c9c9c9;
  font-size: 20px;
  font-weight: bold;
  padding-top: 10px;
}
.main-slider .slider-content .product-count {
  font-family: 'Montserrat', sans-serif;
  color: white;
  font-size: 20px;
  padding-top: 10px;
  z-index: -1;
}
